/**
 * promise
 */
import axios from 'axios'
// import { BASE_URL_TEST } from '../api'
// import qs from 'qs'

export default function fetch1 (baseUrl, url = '', data = {}, method = 'get') {
  method = method.toLowerCase()
  let ops = { data: data }

  // console.log(url)

  // (...)
  let options = {
    url,
    ...ops,
    method
  }

  return new Promise((resolve, reject) => {
    // axios
    const instance = axios.create({
      // 
      baseURL: baseUrl,
      // 
      timeout: 30000,
      // cookie
      withCredentials: true,
      crossDomain: true,
      headers: {
        'Content-Type': 'application/json;charset=UTF-8',
        'X-Requested-With': 'XMLHttpRequest'
      }
    })
    // 
    instance(options)
      .then(({ data }) => {
        // 
        let isLogin = true
        if (data && data.resultCode === '1' && data.resultStatus === '010000') {
          isLogin = false
        }
        if (!isLogin) {
          let returnurl = window.location.href
          let loginUrl = '//plogin.m.jd.com/user/login.action?appid=100&returnurl='
          window.location.href = loginUrl + encodeURIComponent(returnurl)
          return false
        }
        return resolve(data)
      })
      .catch(error => {
        // ,
        if (error.response) {
          // let resError = error.response
          let resCode = error.status
          let resMsg = error.message
          let err = { code: resCode, respMsg: resMsg }
          // console.log('')
          return reject(err)
        } else {
          // console.log('')
          let err = { type: 'canceled', respMsg: '' }
          return reject(err)
        }
      })
  })
}